import { TaskType } from "../lib/types";

export default async function Page() {
	try {
		const data = await fetch("https://jsonplaceholder.typicode.com/todos")
			.then((returnData) => returnData.json())
			.catch((err) => {
				throw new Error(err);
			});
		return (
			<ul className="flex flex-col gap-2">
				{data.map((task: TaskType) => (
					<div
						key={`${task.userId}-${task.id}`}
						className="m-2 p-4 border-1 rounded-2xl">
						{task.completed ? (
							<input type="checkbox" checked readOnly />
						) : (
							<input type="checkbox" readOnly />
						)}
						<label className="m-4 text-3xl">{task.title}</label>
						<span>
							<p>User ID: {task.userId}</p>
							<p>Task ID: {task.id}</p>
						</span>
					</div>
				))}
			</ul>
		);
	} catch (err) {
		return (
			<div>
				<h1 className="text-6xl">Something went wrong...</h1>
				<p>An error occurred when loading the data</p>
			</div>
		);
	}
}
